.sobre-mim {
  padding-top: 50px;
  max-width: 1200px;
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-areas:
    'tittle tittle'
    'description skills';
  grid-template-rows: auto 1fr;
  grid-template-columns: 1fr 1fr;
  gap: 64px;
}

.description {
  grid-area: description;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.description__text {
  font-size: 1.5rem;
  line-height: 150%;
  text-align: justify;
}

.skills {
  grid-area: skills;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.description__tittle, .skills__tittle {
  font-size: 48px;
  color: var(--primary-color);
  text-align: center;
  font-family: 'Poppins', sans-serif;
}

.skills__container {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

.skills__icon {
  width: 100px;
  height: 100px;
  border-radius: 24px;
  background-color: var(--icon-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 12px;
}

.skills__icon i {
  font-size: 2rem;
}

.skills__icon span {
  font-weight: 700;
  font-size: 1rem;
}
